
import {mapState, ModuleTree, Store, useStore} from "vuex";
import {computed, defineComponent, reactive} from "vue";
import {FlexScheduleDay, FlexScheduleMutation} from "@/store/flexScheduleModule";
import Modal from "@/components/common/Modal.vue";
// import {key} from "@/store";
import Remote from '@/remote/scheduleRequest'
import {CostElement, EMPPosition, ShiftType} from "@/store/lookupModule";
import CustomSelect from "@/components/common/CustomSelect.vue";
export default defineComponent({
    name: "EditFlexSchedule",
    components: {
        Modal,
        CustomSelect
    },
    props: {
        isEdit: {
            type: Boolean
        }
    },
    data() {
        return {
            flexScheduleDay: {},
            selectedPosition: <EMPPosition>{},
            selectedShift: <ShiftType>{},
            selectedCostElement1: <CostElement>{},
            selectedCostElement2: <CostElement>{},
            day: <FlexScheduleDay>{},
        }
    },
    setup() {
        // console.log('setup');
        const store = useStore()
        // const flexScheduleModule = getModule
        // const state = reactive({
        //   day: computed(() => store.state.flexSchedule.flexScheduleDay),
        // });
        //
        // console.log('state', state)
        // return {state}
        return {
            // day: computed(() => {
            //     console.log('test', store.state)
            //     return store.state.FlexSchedule.flexScheduleDay
            // }),

            shiftOptions: computed(() => {
                console.log('store.state.LookUp.shift', store.state.LookUp.shifts)
                return store.state.LookUp.shifts
            }),
            costElementOptions: computed(() => {
                console.log('costElementOptions', store.state.LookUp.costElements)
                return store.state.LookUp.costElements
            }),
        }
    },
    computed: {
        positionOptions(): any {
            let options = this.$store.state.LookUp.positions;
            if (options.length === 1) {
                (this as any).selectedPosition = options[0];
            }
            return options
        },
    },
    // computed: mapState([
    //   // map this.count to store.state.count
    //   'flexSchedule/startTime'
    // ]),
    mounted() {
        // let startTime = this.$store.state.FlexScheduleModule.startTime;
        let startTime = this.$store;
        this.day = this.$store.state.FlexSchedule.flexScheduleDay
        // console.log('state', startTime)
        console.log('edit flex schedule mounted')
    },

    // computed: {
    //   ...mapState({
    //     day: (state: State) => state.flexSchedule.flexScheduleDay
    //   }),
    // },

    methods: {
        selectCostElement1(costElement1: any) {
            this.selectedCostElement1 = costElement1;
        },
        selectCostElement2(costElement2: any) {
            this.selectedCostElement2 = costElement2;
        },
        selectShift(shift: any) {
            this.selectedShift = shift;
        },
        selectPositionCustom(position: any) {
            console.log('position', position)
            this.selectedPosition = position
        },

        selectPosition($event: any, position: EMPPosition) {
            this.selectedPosition = position
            $event.target.blur()
            let b = (this.$refs.selectRef as any);
            b.blur();
        },
        clearForm() {
            this.$store.commit(FlexScheduleMutation.clearForm)
        },

        cancel() {
            this.$store.commit(FlexScheduleMutation.clearForm)
            this.toggleShow();
        },

        updateSche() {
            let putData = {
                EmployeeNo: this.$store.state.FlexSchedule.selectedFlexDay.employeeNo,
                Date: this.$store.state.FlexSchedule.calendarFocusDate.day(this.$store.state.FlexSchedule.selectedFlexDay.dayInWeek).format('MM/DD/YYYY'),
                StartTime: this.day.startTime,
                EndTime: this.day.endTime,
                Hours: this.day.hours,
                ScheduleData: {
                    ShiftId: this.selectedShift.shiftType,
                    BreakLength: this.day.breakLength,
                    Position : this.selectedPosition.positionCode,
                    CostElement1: this.selectedCostElement1 ? this.selectedCostElement1.id : undefined,
                    CostElement2: this.selectedCostElement1 ? this.selectedCostElement2.id : undefined
                }
            }

            // console.log('selectedFlexDay', this.$store.state.FlexSchedule.selectedFlexDay)

            Remote.UpdateFlexSchedule(this.$store.state.FlexSchedule.selectedFlexDay.id, putData).then(res => {
                //todo what happen after update
                console.log('res', res)
            })

            // console.log('form', this.$store.state.FlexSchedule.flexScheduleDay)
            this.$store.commit(FlexScheduleMutation.saveScheduleDayToWeek)
            this.toggleShow();
        },

        createSche() {
            let postData = [{
                EmployeeNo: this.$store.state.FlexSchedule.selectedFlexDay.employeeNo,
                Date: this.$store.state.FlexSchedule.calendarFocusDate.day(this.$store.state.FlexSchedule.selectedFlexDay.dayInWeek).format('MM/DD/YYYY'),
                StartTime: this.day.startTime,
                EndTime: this.day.endTime,
                Hours: this.day.hours,
                PublishStatus: 'Draft',
                ScheduleData: {
                    ShiftId: this.selectedShift.shiftType,
                    BreakLength: this.day.breakLength,
                    Position : this.selectedPosition.positionCode,
                    CostElement1: this.selectedCostElement1.id,
                    CostElement2: this.selectedCostElement2.id
                }
            }]
            //
            Remote.CreateFlexSchedule(postData).then(res => {
                //todo what happen after create
                //console.log('res', res)
            })

            // console.log('form', this.$store.state.FlexSchedule.flexScheduleDay)
            this.$store.commit(FlexScheduleMutation.saveScheduleDayToWeek)
            this.toggleShow();
        },
        openEdit() {
            // todo load position

            // todo load shift
            let selectedShift
            if (this.$store.state.FlexSchedule.selectedFlexDay.ScheduleData) {
                selectedShift = this.shiftOptions.find((option: ShiftType) => {
                    return option.shiftType ===  this.$store.state.FlexSchedule.selectedFlexDay.ScheduleData.ShiftId
                })
            }


            if (selectedShift) {
                this.selectedShift = selectedShift;
            }

            // todo load custom element

            if (this.$store.state.FlexSchedule.selectedFlexDay.ScheduleData) {
                this.selectedCostElement1 = this.costElementOptions.find((option: CostElement) => {
                    return option.id === this.$store.state.FlexSchedule.selectedFlexDay.ScheduleData.CostElement1
                })

                this.selectedCostElement2 = this.costElementOptions.find((option: CostElement) => {
                    return option.id === this.$store.state.FlexSchedule.selectedFlexDay.ScheduleData.CostElement2
                })
            }

            console.log('this.selectedShift', this.selectedShift);
            this.day = this.$store.state.FlexSchedule.flexScheduleDay;
            (this.$refs.ModalRef as any).toggleShow();
        },
        toggleShow() {
            (this.$refs.ModalRef as any).toggleShow();

        },

    }


})
